
table, th, td {    
    border:1px solid black ;  
    margin-left: auto;  
    margin-right: auto;  
    border-collapse: collapse;    
    width: 500px;  
    text-align: center;  
    font-size: 20px;  
    max-width: 80%;
  }  
  .center {
    width: 50%;
    margin: auto;
    padding: 10px;
    text-align: center;
  }
  .student-container {
    max-width: 500px; /* Adjust as needed */
     margin: 0 auto; /* Center the container horizontally */
    padding: 20px;
     background-color: #b276c6; 
     border-radius: 8px; 
     box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
   }  
  
   .container h2 {
    color: #333; 
   } 
  
   .container hr {
    border-top: 1px solid #ccc;
     margin: 20px 0; 
